





































































































































































































































































































































.box {
    position: relative;
    height: calc(100vh - 80px);
    .box-body {
        margin: 20px;
        margin-bottom: 17px;
        background: #fff;
        overflow: auto;
        height: calc(100vh - 179px);
    }
}

.item {
    padding-bottom: 28px;
}
.title {
    display: flex;
    align-items: center;
    padding-bottom: 26px;
    span {
        font-size: 16px;
        font-family: PingFangSC-Medium, PingFang SC;
        font-weight: 500;
        color: #333333;
        line-height: 22px;
    }
    .line {
        display: inline-block;
        width: 3px;
        height: 16px;
        margin-right: 10px;
        background: rgba(251, 71, 2, 1);
    }
}

.box_footer {
    position: absolute;
    margin-top: 20px;
    width: 100%;
    bottom: 0;
    height: 81px;
    background: white;
    .box_footer_content {
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: flex-end;
    }
}

.box-item {
    padding: 0 108px;
    font-size: 14px;
    font-family: PingFangSC-Regular, PingFang SC;
    font-weight: 400;
    color: #333333;
    flex-wrap: wrap;
    .li {
        width: 33.33%;
        display: flex;
        align-items: center;
        margin: 10px 0;
    }
    .label {
        width: 61px;
        text-align: right;
        padding-right: 12px;
    }
}
